.App {
  text-align: inline block;
  margin: auto;
  width: 800px;
  background-color: white;
}

* {
	font-family: 'Raleway', sans-serif;
	font-size: 104%;
}

body,#root{margin:0;padding:0;font-family:sans-serif;height:100%;background: white}input,textarea{font-family:Arial}.login{width:100%;height:100%;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center; margin-top: 15px}.login-form{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-direction:column;flex-direction:column;}.login-form input{max-width:100%;border-top:none;border-left:none;border-right:none;height:20px;line-height:20px;font-size:20px;margin-top: 15px;border-bottom:solid 2px #b3b2ca;-webkit-transition:all .23s ease-in;-o-transition:all .23s ease-in;transition:all .23s ease-in}.login-form input:focus{border-bottom:solid 2px #5d5d8a;outline:none}.login-form .error{text-align:center;margin:5px 0;padding:5px 10px;color:#c92c43}

button {
	padding: 3px 4px;
}

@media only screen and (max-width: 480px) {
	.App {
	  text-align: inline block;
	  margin: auto;
	  width: 480px;
	}
}


